import Base from './base'

class Order extends Base {
  constructor () {
    super()
  }

  // 
  generateOrder (data) {
    return this.getAxios({
      url: '/product/order',
      method: 'POST',
      data: data
    })
  }


  // 
  payment (id) {
    return this.getAxios({
      url: '/product/order/ordering',
      method: 'POST',
      data: {
        id
      }
    })
  }

  // 
  getOrderList (username) {
    return this.getAxios({
      url: '/product/order/all',
      method: 'POST',
      data: {
        username
      }
    })
  }
}

export default Order
